import {mapState, mapActions} from 'vuex'
import helper from '@/mixins/helper'
import tool from '@/utils/tool'
import TabbarBottom from "@/components/TabbarBottom"
import SearchBox from "@/components/SearchBox"
import Banner from "@/components/Banner"
import GoodsItem from "@/components/GoodsItem"
import GoodsSort from "@/components/GoodsSort"
import ScrollToTop from "@/components/ScrollToTop"
import NoData from "@/components/NoData"
import SpinnerIos from "@/components/SpinnerIos"

export default {
    name: "home",
    mixins: [helper],
    components: { TabbarBottom, SearchBox, Banner, GoodsSort, GoodsItem, ScrollToTop, NoData, SpinnerIos },
    data() {
        return {
            showGoodsSort: false,
            showTop: false,
            scrollTop: 0,
            isLoading: false,//首屏加载
            isLoadingMore: false, //分页加载中
            nomore: false,
            noDataTxt: "暂无数据",
            loadingTxt: "加载中...",
            saleNav: [
                {
                    icon: "jinrishangxin",
                    label: "今日上新",
                    iconStyle: "background: -webkit-linear-gradient(to bottom, #FF9BBE, #FE6289);background: linear-gradient(to bottom, #FF9BBE, #FE6289)",
                    color1: "FF9BBE",
                    color2: "FE6289",
                },
                {
                    icon: "liwu",
                    label: "9.9包邮",
                    iconStyle: "background: -webkit-linear-gradient(to bottom, #FCC190, #F78E57);background: linear-gradient(to bottom, #FCC190, #F78E57)",
                    color1: "FCC190",
                    color2: "F78E57",
                },
                {
                    icon: "haoquanyouxuan",
                    label: "好券优选",
                    iconStyle: "background: -webkit-linear-gradient(to bottom, #D5A3FB, #AA6AF6);background: linear-gradient(to bottom, #D5A3FB, #AA6AF6)",
                    color1: "D5A3FB",
                    color2: "AA6AF6",
                },
                {
                    icon: "renqipaihang-xuanzhong",
                    label: "人气宝贝",
                    iconStyle: "background: -webkit-linear-gradient(to bottom, #9AC9F7, #6199EC);background: linear-gradient(to bottom, #9AC9F7, #6199EC)",
                    color1: "9AC9F7",
                    color2: "6199EC",
                },
                {
                    icon: "baokuan",
                    label: "品牌爆款",
                    iconStyle: "background: -webkit-linear-gradient(to bottom, #FB9887, #F65F4F);background: linear-gradient(to bottom, #FB9887, #F65F4F)",
                    color1: "FB9887",
                    color2: "F65F4F",
                },
            ],
            pannel1: [
                {
                    icon: "icon-qianggou",
                    title: "淘抢购",
                    desc: "限时限量汇全网好货",
                    label: "",
                    img: "static/img/demo/qianggou1.png",
                    itemStyle: "background: -webkit-linear-gradient(to bottom, #FFF5F5, #FFE7E7);background: linear-gradient(to bottom, #FFF5F5, #FFE7E7);",
                    color: "#ED3C26",
                },
                {
                    icon: "icon-zhonghuasuan0060",
                    title: "聚划算",
                    desc: "好品味从挑剔开始",
                    label: "品质生活指南",
                    img: "static/img/demo/huasuan.png",
                    itemStyle: "background: -webkit-linear-gradient(to bottom, #FFFCF3, #FFF7E4);background: linear-gradient(to bottom, #FFFCF3, #FFF7E4);",
                    color: "#ED3C26"
                },
                {
                    icon: "icon-bimaiqingdanicon",
                    title: "必买清单",
                    desc: "都帮您整理好啦",
                    label: "想买的都在这",
                    img: "static/img/demo/bimai.png",
                    itemStyle: "background: -webkit-linear-gradient(to bottom, #F9FDFF, #F0FBFF);background: linear-gradient(to bottom, #F9FDFF, #F0FBFF);",
                    color: "#ED3C26"
                },
            ],
            pannel2: [
                {
                    title: "精品女装",
                    desc: "限时抢",
                    color: "#ED3C26",
                    img1: "static/img/demo/nvzhuang1.png",
                    img2: "static/img/demo/nvzhuang2.png",
                },
                {
                    title: "品牌男装",
                    desc: "高颜值好物",
                    color: "#26A6CC",
                    img1: "static/img/demo/nanzhuang1.png",
                    img2: "static/img/demo/nanzhuang2.png",
                },
                {
                    title: "家居清洁",
                    desc: "都整理好啦",
                    color: "#50E3C2",
                    img1: "static/img/demo/jiaju1.png",
                    img2: "static/img/demo/jiaju2.png",
                },
                {
                    title: "化妆美肤",
                    desc: "2018流行啥",
                    color: "#B8E986",
                    img1: "static/img/demo/meifu1.png",
                    img2: "static/img/demo/meifu2.png",
                },
            ],
            list: [],
            showTabBar: false
        }
    },
    computed: {
        ...mapState({

        })
    },
    mounted() {
        this.getList()
        this.$refs.listBox.addEventListener('scroll', this.handleScroll, true)
    },
    beforeRouteLeave(to, from, next) {
        next()
        this.$refs.listBox.removeEventListener('scroll', this.handleScroll, true)
        this.showTabBar = false
    },
    beforeDestroy() {
        this.$refs.listBox.removeEventListener('scroll', this.handleScroll, true)
    },
    activated() {
        this.$refs.listBox.addEventListener('scroll', this.handleScroll, true)
        this.$refs.listBox.scrollTop = this.scrollTop
        this.showTabBar = true
    },
    methods: {
        getList() {
            this.isLoading = true
            setTimeout(() => {
                this.list = [{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},]
                this.isLoading = false
            }, 2000)
        },
        handleCateItem(data) {
            this.showGoodsSort = data.index != 0
        },
        handleSort(val) {
            console.log(val)
        },
        handleScroll() {
            let scrollHeight = this.$refs.listBox.scrollHeight;//列表真实高度
            let clientHeight = this.$refs.listBox.clientHeight;//列表可视高度
            let scrollTop = this.$refs.listBox.scrollTop;//列表滚动高度
            // console.log(`scrollHeight: ${scrollHeight}, clientHeight: ${clientHeight}, scrollTop: ${scrollTop}, ${clientHeight + scrollTop >= scrollHeight}`)
            this.scrollTop = scrollTop
            this.showTop = scrollTop > 300
            if (clientHeight + scrollTop >= scrollHeight && !this.isLoadingMore && !this.nomore && !this.isLoading) {
                console.log("加载更多")
                this.isLoadingMore = true
                setTimeout(() => {
                    this.list = this.list.concat([{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},{}, {},])
                    this.isLoadingMore = false
                }, 2000)
            }
        },
        toTop() {
            let timer = setInterval(() => {
                this.$refs.listBox.scrollTop = this.$refs.listBox.scrollTop - 50;
                if (this.$refs.listBox.scrollTop === 0) {
                    clearInterval(timer);
                }
            }, 1);

        },
    },
}



