import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
    name: "Banner",
    components: {
        swiper, swiperSlide
    },
    props: {
        bannerList: {
            type: Array,
            default: function () {
                return [
                    "static/img/demo/banner.png",
                ]
            }
        },
        width: {
            type: [String, Number],
            default: 7.5
        },
        height: {
            type: [String, Number],
            default: 4.8
        },
        showPagination: {
            type: [Boolean],
            default: false
        }
    },
    data() {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    type: 'custom',
                    renderCustom: (swiper, current, total) => {
                        const activeStyle = 'background: #f00;opacity: 1;margin-left: 5px;'
                        const normalStyle = "margin-left: 5px;"
                        let style = ''
                        let html = ''
                        for (let i = 1; i <= total; i++) {
                            style = i == current ? activeStyle : normalStyle
                            html += "<span class='swiper-pagination-bullet' style='" + style + "'></span>"
                        }
                        return html
                    }
                },
                loop: false,
            },
        }
    },
    mounted() {

    },
    beforeDestroy() {

    },
    methods: {

    }
}



